.main {
  .banner {
    min-height: 91.8vh;
    background-image: url(../img/background.png);
    background-position: right;
    background-size: cover;
    background-repeat: no-repeat;
    .container {
      padding: 50px 100px;
    }
    .main-block {
      max-width: 700px;
      h2 {
        font-size: 65px;
        font-weight: 700;
        line-height: 89px;
      }
      p {
        display: block;
        font-size: 27px;
        font-weight: 300;
        line-height: 38px;
        margin-bottom: 40px;
      }
    }
    .about-btn {
      background-color: $primaryColor;
      color: #ffffff;
      padding: 20px 10px;
      border-radius: 40px;
      max-width: 250px;
      text-align: center;
      font-size: 22px;
      font-weight: 300;
      line-height: 26px;
      margin-bottom: 45px;
    }
    .stats-block_container {
      display: flex;
      .stats-block {
        border-right: 1px solid $secondaryColor;
        border-left: 1px solid $secondaryColor;
        text-align: center;
        padding: 20px 30px;
        .stats-block_year {
          display: block;
          font-size: 32px;
          font-weight: 600;
          line-height: 39px;
          margin-bottom: 25px;
        }
        .stats-block_text {
          display: block;
          font-size: 18px;
          font-weight: 400;
          line-height: 24px;
          max-width: 185px;
          width: max-content;
        }
      }
    }
  }
}
