@import "_var";
/* Normalize CSS */
/* Adapted from Normalize.css v8.0.1 | MIT License | github.com/necolas/normalize.css */

/* Document */
html {
  line-height: 1.15; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

/* Sections */
body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

/* Grouping content */
p {
  margin: 0;
}

/* Text-level semantics */
a {
  background-color: transparent;
}

/* Embedded content */
img {
  border-style: none;
}

/* Forms */
button,
input,
select,
textarea {
  font-family: inherit; /* 1 */
  font-size: 100%; /* 1 */
  line-height: 1.15; /* 1 */
  margin: 0; /* 2 */
}

/* Reset list styles */
ul,
ol {
  margin: 0;
  padding: 0;
}

/* Reset anchor styles */
a {
  text-decoration: none;
  color: inherit;
}

/* Tables */
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Full-width border box */
*,
*::before,
*::after {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  font-style: normal;
}
@import "_header";
@import "_banner";
