.header {
  .container {
    width: 100%;
    padding: 20px 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
  }
  .logo {
    font-size: 28.6px;
    line-height: 38.04px;
    color: $primaryColor;
  }
  .menu {
    display: grid;
    gap: 80px;
    grid-template-columns: repeat(6, auto);

    a {
      font-size: 22px;
      line-height: 26px;
      text-align: left;

      .arrow-down {
        width: 14px;
        height: 14px;
        margin-left: 6px;
      }
    }
  }
}
